<template>
  <div class="home" v-if="!!mainData">
    <!-- 导航栏按钮 -->
    <div class="fixedtab">
      <van-nav-bar
        title="淘金金"
        left-text="退出"
        @click-left="onClickLeft()"
        @click-right="onClickRight()"
      >
      <div slot="left"><img src="./../../assets/icon_shutdown@3x.png" alt=""></div>
        <div slot="right">{{rightName}}</div>
      </van-nav-bar>

      <!-- tab选项卡 -->
      <van-tabs v-model="active" sticky>
        <van-tab v-for="(item,index) in tabArr" :key="index">
          <div @click="changeSelect(item,index)" slot="title">
            <span>{{item.name}}</span>
            <span :class="{act:active==index}"></span>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- 折线图 -->
      <div v-if="active==0" id="map"></div>

      <!-- 列表组件 -->
      <datam :listitem="mainData"></datam>
    </van-pull-refresh>
    <!-- 点击退出，弹出层 -->
    <van-actionsheet
      v-model="showQuit"
      :actions="actions"
      cancel-text="取消"
      @select="onSelect"
      @cancel="onCancel"
    />
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import datam from "./dataList.vue";

import api from "./../common/api.js";
// import { Tab, Tabs,PullRefresh,NavBar} from 'vant';

// Vue.use(Tab).use(Tabs);
// Vue.use(NavBar);Vue.use(PullRefresh);
// // Vue.use(ActionSheet);

export default {
  data() {
    return {
      active: 0, //选项卡选项
      tabObj: {
        '首页': api.param.indexHome,
        '助力': api.param.boostindex,
        '赚赚': api.param.earnindex,
        "1元拼团": api.param.indexOnePrice
      },
      tabArr: [], //tab数组
      showQuit: false, //显示弹出层
      isLoading: false, //加载中
      mainData: {},
      actions: [
        {
          name: "确认退出"
        }
      ],
      myChart: null,
      linmapData: {
        title: {
          text: "196.45",
          bottom: 1,
          left: "50%",
          textAlign: "center",
          textStyle: {
            fontSize: 40,
             color:'#a6a6a6',
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              // backgroundColor: "#00ffff"
            }
          }
        },
        color: ["#ff6632", "#eeeeee"],
          // color: ["rgba(255,148,111,0.6)", "#eeeeee",#ffd328],
        legend: {
          data: [
            {
              name: "今日",
              // 强制设置图形为圆。
              icon: "circle"
            },
            {
              name: "昨日",
              // 强制设置图形为圆。
              icon: "circle"
            }
          ],
          bottom: 56
          // right: 5
        },
        grid: {
          left: "1%",
          right: "2%",
          top: "11%",
          bottom: "30%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: ["1", "2", "3", "4", "5", "6", "7"],
            splitLine: {
              lineStyle: {
                // color:'#F1F4FB'
              }
            }
          }
        ],
        yAxis: [
          {
            name: "",
            type: "value",
            axisLine: {
              //y轴
              show: false
            },
            splitLine: {
              lineStyle: {
                color: "#F1F4FB",
                width: 1.2
              }
            }
          }
        ],
        series: [
          {
            name: "今日",
            type: "line",
            smooth: true,
            stack: "总量5",
            zlevel:3,
            // label: {
            //   normal: {
            //     show: true,
            //     position: "top"
            //   }
            // },
            areaStyle: {
              color: "rgba(255,102,50,1)"//改变区域颜色
            },
            lineStyle: {
              width: 2, // 线宽是0
              color: "#b82e00" // 线的颜色是透明的
            },
            itemStyle: {
              opacity: 1,
                 color: "#b82e00" // 线的颜色是透明的
            },
            data: [820, 932, 901, 934, 1290, 1330, 1320]
            // markPoint: {
            //     symbolSize:70,
            //   data: [
            //     {
            //       type: "max",
            //       name: "最大值",
            //       itemStyle: {
            //         color: "#ff6632"
            //       }
            //     }
            //   ]
            // }
          },
          {
            name: "昨日",
            type: "line",
            smooth: true,
            stack: "总量1",
            zlevel:2,
            areaStyle: {
              color: "#eeeeee" //改变区域颜色
            },
            itemStyle: {
              opacity: 1,
              color: "#cdcdcd" // 线的颜色是透明的
            },
            lineStyle: {
              width: 2, // 线宽是0
              color: "#cdcdcd" // 线的颜色是透明的
            },
            data: [120, 132, 101, 134, 90, 230, 210]
            // markPoint: {
            //     symbolSize:70,
            //   data: [
            //     {
            //       type: "max",
            //       name: "最大值",
            //       itemStyle: {
            //         color: "#ffd328"
            //       }
            //     }
            //   ]
            // }
          }
        ]
      }
    };
  },
  computed: mapGetters(["rightName"]),
  components: {
    datam
  },
  mounted() {
    document.documentElement.scrollTop = 0;
    document.body.scrollTop = 0;
 
    window.sessionStorage.removeItem('seceondClick')
    this.initData();
    // location.href="taojiji://zhuan.taojiji.com";
    // location.href="taojiji://"


  },
  methods: {
    initData() {
      this.$store.dispatch("showloading");
      this.$fetch.getData(api.apiUrl, api.param.TopBar, mdata => {
        this.tabArr = mdata.data;

        this.$fetch.getData(
          api.apiUrl,
          this.tabObj[this.tabArr[0].name],
          data => {
            this.$store.dispatch("showloading");
             window.localStorage.setItem("reloadItem",JSON.stringify(this.tabArr[0]));
            if (this.tabArr[0].name == "首页") {
              this.dealdata(data, this.tabArr[0].name);
            } else {
              this.dealdata(this)(data, this.tabArr[0].name);
            }
          }
        );
      });
    },
    dealdata(data, name) {
      window.localStorage.setItem("selectItem", name);
      var savData = savData = window.localStorage.getItem("saveSelect"+name)
            ? JSON.parse(window.localStorage.getItem("saveSelect"+name))
            : null;;
    
      if (name != '首页') {
        if (!!savData) {
          savData.data.forEach((v, index) => {
            for (let key in v.data) {
              data.data[index].data[key].itemSelected =
                v.data[key].itemSelected;
            }
          });
        } else {
          data.data.forEach((v, index) => {
            for (let key in v.data) {
              v.data[key].itemSelected = false;
            }
          });
        }

        this.mainData = data;
      } else {

        if (!!savData) {
          savData.data.forEach((v, index) => {
            if (index == 0) {
              for (let key in v.data) {
                data.data[index].data[key].itemSelected =
                  v.data[key].itemSelected;
              }
            } else {
              for (let key in v) {
                if (key != "title") {
                  data.data[index][key].itemSelected = v[key].itemSelected;
                }
              }
            }
          });
        } else {
          data.data.forEach((v, index) => {
            if (index == 0) {
              for (let key in v.data) {
                v.data[key].itemSelected = false;
              }
            } else {
              for (let key in v) {
                if (key != "title") {
                  v[key].itemSelected = false;
                }
              }
            }
          });
        }
        this.mainData = data;

        this.mainData.isshow = true;

        this.mainData.chartList.today = this.mainData.chartList.today;
        this.mainData.chartList.yesterday = this.mainData.chartList.yesterday;
        this.myChart = this.$echarts.init(document.getElementById("map"));

        // 使用刚指定的配置项和数据显示图表。
        var xArr = [],
          yesterdayValueArr = [],
          todayValueArr = [];
        this.mainData.chartList.yesterday.forEach((v, index) => {
          xArr.push(parseInt(v.time));
          if (v.total.indexOf(",") > -1) {
            v.total=v.total.replace(/,/g, "")
          }
          yesterdayValueArr.push(parseFloat(v.total));
        });
        this.mainData.chartList.today.forEach((v, index) => {
                    if (v.total.indexOf(",") > -1) {
            v.total=v.total.replace(/,/g, "")
          }
          todayValueArr.push(parseFloat(v.total));
        });

        if (
          Math.max(...yesterdayValueArr) > 10000 ||
          Math.max(...todayValueArr) > 10000
        ) {
          yesterdayValueArr = yesterdayValueArr.map(v => {
            return (v / 10000).toFixed(6);
          });
          todayValueArr = todayValueArr.map(v => {
            return (v / 10000).toFixed(6);
          });

          this.linmapData.yAxis[0].name = "万";
        }

        this.linmapData.title.text = this.mainData.chartList.perMinuteOrderNum;
        this.linmapData.xAxis[0].data = xArr;
        this.linmapData.series[1].data = yesterdayValueArr;
        this.linmapData.series[0].data = todayValueArr;
        this.myChart.setOption(this.linmapData);
        this.$forceUpdate();
      }
    },
    changeSelect(item, index) {
      window.localStorage.setItem("reloadItem",JSON.stringify(item));
      var obj = (obj = {
        url: api.apiUrl,
        data: this.tabObj[item.name]
      });

      this.$store.dispatch("showloading");
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0;
      this.$fetch.getData(obj.url, obj.data, data => {
        this.active = index;
        this.$store.dispatch("showloading");
        if (item.name == "首页") {
          this.$options.methods.dealdata.bind(this)(data,item.name);
        } else {
          this.$options.methods.dealdata.bind(this)(data,item.name);
        }
      });
    },
    onClickLeft() {
      this.showQuit = true;
    },
    onClickRight() {
      this.$store.dispatch("showhight");
    },
    onSelect(item) {
      // 点击选项时默认不会关闭菜单，可以手动关闭
      window.webkit.messageHandlers.loginout.postMessage("loginout");
    },
    onCancel() {
      this.showQuit = false;
    },
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.$options.methods.changeSelect.bind(this)(JSON.parse(window.localStorage.getItem("reloadItem")),this.active);
      }, 500);
    }
  }
};
</script>
<style lang="stylus" scoped>
#map {
  width: 100vw;
  height: 8rem;
  padding-top: 20px;
}
</style>



